.App-header {
  width: 100%;
  height: 100%;
  display: flex;
}

h1 {
  margin: 0;
  font-size: 34px;
  text-transform: uppercase;
  line-height: 25px;
}

section {
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-right: 300px;
}

nav {
  height: 100%;
  width: 300px;
  position: fixed;
  display: flex;
  flex-direction: column;
  z-index: 1;
  top: 0;
  right: 0;
  background-color: #001420;
}

.logo-bar {
  width: 100%;
  height: 175px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  border-bottom: 1px solid #052846;
  background-color: #000000;
}

.nav-bar {
  width: 100%;
  display: flex;
  flex: 1 1 auto;
  flex-direction: column;
  justify-content: space-evenly;
  align-items: center;
  position: relative;
}

.nav-bar a {
  text-decoration: none;
}

.nav-button {
  width: 295px;
  height: 190px;
  padding-top: 5px;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  color: #ffffff;
  text-transform: uppercase;
}

.nav-button h3 {
  margin: 20px 0 0;
    font-weight: 500;
}

.line {
  width: 94%;
  height: 1px;
  display: block;
  background-color: #D1D1D1;
}

.line-full {
  width: 100%;
  height: 1px;
  display: block;
  background-color: #D1D1D1;
}

.line-box {
  position: relative;
  width: 100%;
  height: 1px;
  display: flex;
  align-items: center;
  justify-content: center;
}

.line-box:after {
  content: '';
  position: absolute;
  width: 52px;
  height: 5px;
  background: #ff693c;
  top: -2px;
}

.line-dark {
  width: 65%;
  height: 1px;
  display: block;
  background-color: #052846;
}

#appearance {
  background-color: #ffffff;
  width: 100%;
  height: 100vh;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  position: relative;
}

.centered-top-bar {
  width: 94%;
  min-height: 175px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  border-bottom: 1px solid #D1D1D1;
}

.centered-top-bar:after {
  content: '';
  position: absolute;
  width: 52px;
  height: 5px;
  background: #ff693c;
  top: 173px;
}

.centered-machine {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding-top: 25px;
}

.centered-heading {
  height: 100px;
  padding-bottom: 125px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

#video {
  background-color: #f5f5f5;
  width: 100%;
  height: 100vh;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  position: relative;
}

.video-content {
  display: flex;
  justify-content: center;
  align-items: center;
}

.react-player {
  width: 1200px;
}

#tech {
  background-color: #ffffff;
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  position: relative;
}

.table-content {
  margin: 40px 0 75px;
  width: 1200px;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.m-table {
  margin-top: 40px;
  width: 100%;
}

.m-table table {
  width: 100%;
}

.m-table table tbody {
  margin-top: 20px;
}

.m-table table td {
  padding: 10px 20px;
}

.table-content th {
  font-size: 20px;
  padding: 15px 0;
  border-bottom: 1px solid #E94816;
}

.table-content td:nth-child(2) {
  text-align: right;
}

#about {
  background-color: #f5f5f5;
  width: 100%;
  height: 100vh;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;
  position: relative;
}

.about-content {
  flex: 1 1 auto;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}

.about-logo {
  margin-bottom: 40px;
}

.info-block {
  max-width: 750px;
  white-space: break-spaces;
  margin-top: 40px;
  text-align: justify;
}

.info-block.big{
  font-size: 20px;
}
